*{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dashboard{
    margin-left: 20px;
    margin-top: 10px;
    overflow-y: visible;
}

.button-add{
    background-color: #4CAF50;
}

.button-delete{
    background-color: red;
}

.container{
    margin-top: 20px;
}

.container, .container-values {
    margin-bottom: 20px;
    padding: 0 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.name, .phone{
    -webkit-flex: 2 1;
            flex: 2 1;
    font-size: 20px;
    font-weight: bold;
    color: grey;
}

.delete-buttons {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.name-value, .phone-value {
    -webkit-flex: 2 1;
            flex: 2 1;
    color: black;
    font-size: 16px;
}
.add-page{
    margin-top: 10px;
    padding: 20px 40px;
}

.back-link{
    padding-left: 25px;
    text-decoration: none;
    color: black;
}

.add-form {
    margin-top: 50px;
}

.input-field{
    margin-bottom: 20px;
    width: 250px;
    height: 40px;
    padding: 0 10px;
    border: 1px solid grey;
    border-radius: 3px;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}

.field-label{
    color: grey;
    font-weight: 300;
    font-size: 18px;
}

.display-label-top{
    margin-top: 10px;
    color: #1766A6;
    font-weight: bold;
    font-size: 16px;
}

.display-label{
    color: grey;
    font-size: 16px;
}

.submit-button {
    cursor: pointer;
}

.button-add, .button-delete {
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}
.navbar{
    margin-bottom: 20px;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #3F51B5;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.link {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    font-weight: 200;
    font-size: 20px;
    text-transform: uppercase;
}
